/**
 * Classe que expe o mdulo nativo IntentDigitalHubNativeModule como um mdulo TS, servindo como wrapper para as chamadas nativas, proporcionando typechecking e evitando declarar NativeModules vrias vezes nas pginas que utilizam chamadas nativas. 
 * Funes exportadas a partir de IntentDigitalHubNativeModule.java na pasta da plataforma Android
 */
import { NativeModules } from 'react-native';

//Nome do mdulo registrado na plataforma Android
const { IntentDigitalHubNativeModule } = NativeModules;

//Interface que serve como Wrapper para as funes nativas utilizadas
interface NativeServiceInterface {
   startIntentForResult(commandJson: string, intentPath: string, callback: (result: string) => void): void;
}

export default IntentDigitalHubNativeModule as NativeServiceInterface;